@import '../../styles/colors.module';
@import '../../styles/margins.module';

.container {
  background-color: $white;
  width: 820px;
  margin: 0 auto $m;
  padding: $s $l;
  border-radius: 3px;
  border: none;
  box-shadow: 0 0 1px 1px rgba(0,0,0,0.1);
}

.heading {
  font-weight: 400;
  color: $cl-header;
  margin: 0;
}

.underline {
  border-bottom:0;
  border-top: 1px solid $light-grey;
  margin-bottom: 20px;
}