@import '../../../common/styles/widths.module';
@import '../../../common/styles/margins.module';


.container {
  display: inline-flex;
}

.date-input {
  width: $mediumElement;
  margin-left: 45px;
}

.date-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.time-input {
  width: $narrowElement;
  margin-left: $s;
}

.time-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.time-form {
  display: inline-flex;
  vertical-align: center;
  align-items: center;
  margin-left: $m;
}

.error {
  position: sticky;
}