@import '../../styles/colors.module';
@import '../../styles/margins.module';
@import '../../styles/widths.module';

.container {
  display: flex;
  align-items: flex-start;
  margin-top: $m;
  justify-content: space-between;
  margin-right: $xxl;
}

.textarea-wrapper {
  display: inline-block;
  position: relative;
}

.textarea {
  width: $wideElement;
  height: 170px;
  padding: 6px 10px;
  font: {
    family: "Open Sans", "Arial", sans-serif;
    size: 1rem;
  }
  border-radius: 2px;
  border: 1px solid $light-grey;
  resize: none;
  &:focus {
    outline: none;
    border: 1px solid $cl-header;
    border-radius: 2px;
  }
}

.textarea::placeholder {
  color: $light-grey;
}

.char-limit {
  font: {
    size: 0.8rem;
    style: italic;
    weight: lighter;
  }
  color: $light-grey;
}

.char-counter {
  float: right;
}

.has-error {
  border-color: $salmon;
}