@import '../../styles/colors.module';
@import '../../styles/margins.module';
@import '../../styles/widths.module';

.input-container {
  display: flex;
  align-items: center;
}

.input-wrapper {
  position: relative;
  display: inline-block;
}

.space-between {
  justify-content: space-between;
  margin-right: $xxl;
}

.margin-top {
  margin-top: $m;
}

.input {
  width: $wideElement;
  padding: 6px 10px;
  font: {
    family: "Open Sans", "Arial", sans-serif;
    size: 1rem;
  }
  border-radius: 2px;
  border: 1px solid $light-grey;
  &:focus {
    outline: none;
    border: 1px solid $cl-header;
    border-radius: 2px;
  }
}

.input::placeholder {
  color: $light-grey;
}

.input-text {
  margin-left: $s;
}

.has-error {
  border-color: $salmon;
}

.placeholder {
  color: $light-grey;
}