@import '../../styles/colors.module';
@import '../../styles/margins.module';
@import '../../styles/widths.module';

.select-container {
  margin-top: $m;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-right: $xxl;
}

.select {
  width: 522px;
  padding: 6px 10px;
  font: {
    family: "Open Sans", "Arial", sans-serif;
    size: 1rem;
  }
  -webkit-appearance: none;
  border-radius: 2px;
  border: 1px solid $light-grey;
  background-color: $white;
  &:focus {
    outline: none;
    border: 1px solid $cl-header;
    border-radius: 2px;
  }
}

.description {
  font: {
    size: 0.8rem;
    style: italic;
    weight: lighter;
  }
  color: $light-grey;
}

.placeholder {
  color: $light-grey;
}