@import '../../../styles/colors.module';
@import '../../../styles/margins.module';

.container {
  display: inline-flex;
  vertical-align: middle;
  margin-right: $m;
  position: relative;
}

.input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
.checkmark {
  position: absolute;
  top: 2px;
  left: 0;
  height: 16px;
  width: 16px;
  background-color: $white;
  border: 1px solid $label;
  border-radius: 50%;
}

.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

.container input:checked ~ .checkmark:after {
  display: block;
}

.container .checkmark:after {
  top: 3px;
  left: 3px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: $label;
}

.button-text {
  margin-left: $m;
}