@import '../../common/styles/margins.module';
@import '../../common/styles/widths.module';
@import '../../common/styles/colors.module';

.new-event-form {
  padding: 40px;
}

.category-label-shift {
  position: relative;
  top: -10px;
}

.description-label-shift {
  position: relative;
  top: 5px;
}

.reward-input {
  width: $narrowElement;
  margin-left: 63px;
}

.reward-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.duration-input {
  width: $narrowElement;
  margin-left: 47px;
}

.duration-input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

.submit-button {
  font: {
    family: 'Open Sans', 'Arial', sans-serif;
    size: 1rem;
  }
  padding: 15px 30px;
  color: $white;
  background-color: $orange;
  border: none;
  border-radius: 2px;
}