@import '../../styles/colors.module';

.container {
  margin-left: 10px;
  font-size: 0.8rem;
  position: absolute;
  right: -165px;
  top: 0;
  background: $salmon;
  color: $white;
  padding: 4px 10px;
  border-radius: 2px;
  max-width: 125px;
  text-align: center;
  display: flex;
  align-items: center;

  &.center {
    top: 50%;
    transform: translateY(-50%);
  }
}

.arrow {
  width: 10px;
  height: 10px;
  background: $salmon;
  position: absolute;
  left: -5px;
  transform: rotate(45deg);
}